.dropdown {
  svg:hover {
    cursor: pointer;
  }

  position: relative;

  .languages {
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
    display: none;
    position: absolute;
    right: 0;
    margin-top: 1rem;
    flex-direction: column;
    align-items: center;
    background-color: var(--servicio);

    &.visual {
      display: flex;
    }

    &:before {
      position: inherit;
      content: "";
      width: 2.5rem;
      height: 2.5rem;
      top: calc(50% - 8rem);
      right: calc(50% - 4.1rem);
      transform: rotate(45deg);
      background-color: var(--servicio);
      z-index: -1;
    }

    a {
      width: 100%;
      padding: 1.5rem;
      color: var(--azul);
      font-weight: $bold;
      transition: background-color 0.3s ease;

      &:hover {
        background-color: var(--azul);
        color: $blanco;
      }
    }
  }
}
