.paginacion {
  display: flex;
  justify-content: space-between;
  margin-top: $separacion;
  margin-bottom: 3rem;
  gap: $separacion / 2;

  button {
    background-color: var(--azul);
    color: $blanco;
    padding: 1rem 3rem;
    display: block;
    text-align: center;
    border-radius: 0.5rem;
    border: none;
    font-weight: $bold;
    text-transform: uppercase;
    font-size: 1.5rem;

    @include telefono {
      font-size: 2rem;
    }

    &.ocultar {
      visibility: hidden;
    }

    &:hover {
      cursor: pointer;
    }
  }
}
