html[theme="light-theme"] {
  --bg: #ffffff;
  --letras-principal: #1a1b15;
  --letras-secundario: #1a1b15;
  --azul: #054362;
  --servicio: #cccccc;
  --input-border: #6d7158; }

html[theme="dark-theme"] {
  --bg: #1a1b15;
  --letras-principal: #ffffff;
  --letras-secundario: #1a1b15;
  --azul: #0da6f3;
  --servicio: #ffffff;
  --input-border: #ffffff; }

.text-center {
  text-align: center; }

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem; }

p {
  color: var(--letras-principal);
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: var(--azul);
  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem;
  text-align: center; }

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  /* Hide default HTML checkbox */ }
  .switch input {
    opacity: 0;
    width: 0;
    height: 0; }

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  /* Rounded sliders */ }
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: #ffffff;
    -webkit-transition: 0.4s;
    transition: 0.4s; }
  .slider.round {
    border-radius: 34px; }
    .slider.round:before {
      border-radius: 50%; }

input:checked + .slider {
  background-color: #0da6f3; }

input:focus + .slider {
  box-shadow: 0 0 1px #0da6f3; }

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px); }

.dropdown {
  position: relative; }
  .dropdown svg:hover {
    cursor: pointer; }
  .dropdown .languages {
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
    display: none;
    position: absolute;
    right: 0;
    margin-top: 1rem;
    flex-direction: column;
    align-items: center;
    background-color: var(--servicio); }
    .dropdown .languages.visual {
      display: flex; }
    .dropdown .languages:before {
      position: inherit;
      content: "";
      width: 2.5rem;
      height: 2.5rem;
      top: calc(50% - 8rem);
      right: calc(50% - 4.1rem);
      transform: rotate(45deg);
      background-color: var(--servicio);
      z-index: -1; }
    .dropdown .languages a {
      width: 100%;
      padding: 1.5rem;
      color: var(--azul);
      font-weight: 700;
      transition: background-color 0.3s ease; }
      .dropdown .languages a:hover {
        background-color: var(--azul);
        color: #ffffff; }

body {
  background-color: var(--bg); }

@media (min-width: 768px) {
  .contenedor-estetica {
    height: 100vh;
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr); } }

.contenedor-estetica .imagen {
  height: 30rem;
  background-image: url(../../../build/img/1.webp);
  background-size: cover;
  background-position: center center; }
  @media (min-width: 768px) {
    .contenedor-estetica .imagen {
      height: auto; } }

.app {
  width: 95%;
  margin: 0 auto;
  padding-top: 2.5rem; }
  .app .seccion {
    display: none;
    margin-top: 2.5rem; }

.mostrar-seccion {
  display: block !important; }

.header {
  margin-bottom: 2rem; }
  .header .options {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem; }
    @media (min-width: 480px) {
      .header .options {
        justify-content: space-between; } }

.tabs {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr); }
  .tabs button {
    background-color: var(--servicio);
    font-size: 1.4rem;
    font-weight: 700;
    padding: 1rem;
    text-align: center;
    border-radius: 0.5rem;
    border: none;
    text-transform: uppercase; }
    .tabs button.actual {
      background-color: var(--azul);
      color: #ffffff; }
    .tabs button:hover {
      cursor: pointer; }

.listado-servicios {
  margin-bottom: 2rem; }
  @media (min-width: 768px) {
    .listado-servicios {
      display: grid;
      gap: 2.5rem;
      grid-template-columns: repeat(2, 1fr); } }
  .listado-servicios .servicio {
    background-color: var(--servicio);
    border-radius: 1rem;
    padding: 2.5rem;
    margin: 1rem 0; }
    @media (min-width: 768px) {
      .listado-servicios .servicio {
        margin: 0; } }
    .listado-servicios .servicio p {
      margin: 0 0 1.25rem 0; }
    .listado-servicios .servicio .nombre-servicio {
      color: var(--letras-secundario);
      font-weight: 300; }
    .listado-servicios .servicio .precio-servicio {
      color: var(--azul);
      font-weight: 700;
      font-size: 3rem; }
    .listado-servicios .servicio.seleccionado {
      background-color: var(--azul); }
      .listado-servicios .servicio.seleccionado .nombre-servicio,
      .listado-servicios .servicio.seleccionado .precio-servicio {
        color: #ffffff; }

.paginacion {
  display: flex;
  justify-content: space-between;
  margin-top: 5rem;
  margin-bottom: 3rem;
  gap: 2.5rem; }
  .paginacion button {
    background-color: var(--azul);
    color: #ffffff;
    padding: 1rem 3rem;
    display: block;
    text-align: center;
    border-radius: 0.5rem;
    border: none;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 1.5rem; }
    @media (min-width: 480px) {
      .paginacion button {
        font-size: 2rem; } }
    .paginacion button.ocultar {
      visibility: hidden; }
    .paginacion button:hover {
      cursor: pointer; }

.formulario {
  width: 100%;
  padding: 2rem; }

.campo {
  display: flex;
  align-items: center;
  margin: 2rem 0; }
  .campo label {
    flex: 1;
    color: var(--letras-principal); }
  .campo input {
    flex: 2;
    max-width: 20rem;
    border-radius: 1rem;
    border: 1px solid var(--input-border);
    padding: 1.25rem; }
  @media (min-width: 480px) {
    .campo label {
      flex: 0 0 10rem; }
    .campo input {
      flex: 1;
      max-width: none; } }

.alerta {
  padding: 2.5rem;
  text-align: center;
  color: #ffffff;
  width: 100%;
  display: block; }
  .alerta.error {
    background-color: #c20000; }

.invalidar-cita {
  margin: 2.5rem 0;
  text-transform: uppercase; }

.contenido-resumen {
  margin: 2.5rem 0; }
  .contenido-resumen p {
    margin-bottom: 0.5rem; }
    .contenido-resumen p span {
      font-weight: 700;
      color: var(--azul);
      font-size: 2.4rem; }
  .contenido-resumen .resumen-servicios h3 {
    margin: 2.5rem 0; }
  .contenido-resumen .resumen-servicios .contenedor-servicio {
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid #43453c; }
    .contenido-resumen .resumen-servicios .contenedor-servicio .precio {
      font-weight: 700;
      color: var(--azul);
      font-size: 2.4rem; }

.total {
  text-align: right; }
