.listado-servicios {
  margin-bottom: 2rem;

  @include tablet {
    @include grid(2, $separacion / 2);
  }

  .servicio {
    background-color: var(--servicio);
    border-radius: 1rem;
    padding: $separacion / 2;
    margin: 1rem 0;

    @include tablet {
      margin: 0;
    }

    //evitar el margen por defecto de los <p>
    //reiniciandolo nosotros
    p {
      margin: 0 0 $separacion / 4 0;
    }

    .nombre-servicio {
      color: var(--letras-secundario);
      font-weight: $delgada;
    }

    .precio-servicio {
      color: var(--azul);
      font-weight: $bold;
      font-size: 3rem;
    }

    &.seleccionado {
      background-color: var(--azul);

      .nombre-servicio,
      .precio-servicio {
        color: $blanco;
      }
    }
  }
}
