.formulario {
  width: 100%;
  padding: 2rem;
}

.campo {
  display: flex;
  align-items: center;
  margin: 2rem 0;

  label {
    flex: 1;
    color: var(--letras-principal);
  }

  input {
    flex: 2;
    max-width: 20rem;
    border-radius: 1rem;
    border: 1px solid var(--input-border);
    padding: $separacion / 4;
  }

  @include telefono {
    label {
      flex: 0 0 10rem;
    }

    input {
      flex: 1;
      max-width: none;
    }
  }
}

.alerta {
  padding: $separacion / 2;
  text-align: center;
  color: $blanco;
  width: 100%;
  display: block;

  &.error {
    background-color: rgb(194, 0, 0);
  }
}
