body {
  background-color: var(--bg);
}

.contenedor-estetica {
  @include tablet {
    height: 100vh; //viewport height, toda la ventana gráfica
    @include grid(2, $separacion / 2);
  }

  .imagen {
    height: 30rem;
    background-image: url(../../../build/img/1.webp);
    background-size: cover;
    background-position: center center;
    @include tablet {
      height: auto;
    }
  }
}

.app {
  width: 95%;
  margin: 0 auto;
  padding-top: $separacion / 2;

  .seccion {
    display: none;
    margin-top: $separacion / 2;
  }
}

.mostrar-seccion {
  display: block !important;
}
