.tabs {
  @include grid(3, 2rem);

  button {
    background-color: var(--servicio);
    font-size: 1.4rem;
    font-weight: $bold;
    padding: 1rem;
    text-align: center;
    border-radius: 0.5rem;
    border: none;
    text-transform: uppercase;

    &.actual {
      background-color: var(--azul);
      color: $blanco;
    }

    &:hover {
      cursor: pointer;
    }
  }
}
