// Fuentes
$fuente_principal: "Poppins", sans-serif;

// Tamaño de Media Queries
$telefono: 480px;
$tablet: 768px;
$desktop: 1024px;

// Colores
$azul: #0da6f3;
$azul-oscuro: darken($azul, 30%);
$negro: #1a1b15;
$negro-claro: lighten($negro, 30%);
$blanco: #ffffff;
$blanco-oscuro: darken($blanco, 20%);
$gris: #e1e1e1;

html[theme="light-theme"] {
  --bg: #{$blanco};
  --letras-principal: #{$negro};
  --letras-secundario: #{$negro};
  --azul: #{$azul-oscuro};
  --servicio: #{$blanco-oscuro};
  --input-border: #{$negro-claro};
}

html[theme="dark-theme"] {
  --bg: #{$negro};
  --letras-principal: #{$blanco};
  --letras-secundario: #{$negro};
  --azul: #{$azul};
  --servicio: #{$blanco};
  --input-border: #{$blanco};
}

$separacion: 5rem;

// Fuentes

$delgada: 300;
$regular: 400;
$bold: 700;
$black: 900;
